@import "../../global.scss";

.contact {
  background-color: white;

  .contactCenter {
    display: flex;
    align-items: center;
    margin-top: 20px;
    margin-bottom: 10px;

    h1 {
      display: flex;
      align-items: center;
      justify-content: flex-start;
      padding-left: 15%;
      height: 100px;
      font-weight: 400;
    }
  }

  .container {
    display: flex;

    @include mobile {
      flex-direction: column;
    }

    .left {
      flex: 8;
      overflow: hidden;

      @include mobile {
        // width: 50%;
        // height: 50%;
      }

      img {
        height: 80%;
        width: 80%;
        padding-left: 10%;

        @include mobile {
          // width: 300px;
          // height: 400px;
        }
      }
    }
    .right {
      flex: 4;
      display: flex;
      flex-direction: column;
      align-items: center;

      @include mobile {
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: center;
      }

      .social {
        width: 70%;
        height: 70%;
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        justify-content: space-around;

        @include mobile {
          display: flex;
          flex-direction: row;
        }

        .itemContainer {
          display: flex;
          align-items: center;

          @include mobile {
            display: flex;
            align-items: center;
          }

          a {
            text-decoration: none;
            color: black;

            &:hover {
              color: $footerBG;
            }
          }
        }
      }
    }
  }
}
